import React from "react";
import Skeleton from "react-loading-skeleton";
import useVzlaData from "../hooks/useVzlaData";

import "./styles/Card.css";

const API = "https://covid19.patria.org.ve/api/v1/summary";
const formatNumber = (number) => new Intl.NumberFormat("de-DE").format(number);

const Card =  () => {

    const data = useVzlaData(API);

    return (
        <div className="card--container">
            <div className="card--item">
                <h2>Confirmados</h2>
                <p>{(data.length === 0) ? <Skeleton/> : formatNumber(data.Confirmed.Count)}</p>
            </div>
            <div className="card--item">
                <h2>Activos</h2>
                <p>{(data.length === 0) ? <Skeleton/> : formatNumber(data.Active.Count)}</p>
            </div>
            <div className="card--item">
                <h2>Recuperados</h2>
                <p>{(data.length === 0) ? <Skeleton/> : formatNumber(data.Recovered.Count)}</p>
            </div>
            <div className="card--item">
                <h2>Fallecidos</h2>
                <p>{(data.length === 0) ? <Skeleton/> : formatNumber(data.Deaths.Count)}</p>
            </div>
        </div>
    )
}

export default Card;