import React, {useState, useEffect, useMemo, useRef, useCallback} from "react";
import Skeleton from "react-loading-skeleton";
import "./styles/ListVzla.css";
import "./styles/ListGlobalCase.css";

const API = "https://coronavirus-19-api.herokuapp.com/countries";
const formatNumber = (number) => new Intl.NumberFormat("de-DE").format(number);

const ListGlobalCase = () => {

    const [search, setSearch] = useState("");
    const searchInput = useRef(null);
    const [data, setData] = useState([]);

    useEffect(() => {
        fetch(API)
        .then(response => response.json())
        .then(data => setData(data))
        .catch(error => console.error(error));
    }, [])

    const handleSearch = useCallback(() => {
        setSearch(searchInput.current.value);
    }, [])

    const filterUser = useMemo(() => 
        data.filter((countrie) => {
            return countrie.country.toLowerCase().includes(search.toLowerCase());
        }),
         [data, search]
    )

    return(
        <div className="list--container global-container">
            <h3>Casos por países</h3>
            <input type="text" value={search} ref={searchInput} onChange={handleSearch} placeholder="Buscar por pais"/>
            <ul>
                {(data.length === 0) ? <Skeleton/> : filterUser.map(countrie => {
                    return(
                    <div key={countrie.country}>
                        <h4>
                            <span>{countrie.country}</span>
                            <span>{formatNumber(countrie.cases)}</span>
                        </h4>
                    </div>
                    )
                })}
            </ul>
        </div>
    )
}

export default ListGlobalCase;