import React from "react";
import Skeleton from "react-loading-skeleton";
import useVzlaData from "../hooks/useVzlaData";
import "./styles/ListVzla.css";

const API = "https://covid19.patria.org.ve/api/v1/summary";
const formatNumber = (number) => new Intl.NumberFormat("de-DE").format(number);

const CaseByAge = () => {

    const data = useVzlaData(API);//Hook que me trae la data

    let age;

    if(data.Confirmed){
        age = data.Confirmed.ByAgeRange;
    }
    
    return(
        <div className="list--container">
            <h3>Casos por edad</h3>
            <ul>
                {(data.Confirmed) ? data.Confirmed && Object.entries(age).map(item => (
                <div key={item[0]}>
                    <h4>
                        <span>{item[0]} años</span>
                        <span>{formatNumber(item[1])}</span>
                    </h4>
                </div>
            )) : <Skeleton/>}
            </ul>
        </div>
    )
}

export default CaseByAge;