import {useState, useEffect} from 'react';

const useVzlaData = (url) => {
    const [data, setData] = useState([]);

    useEffect(() => {
        fetch(url)
        .then(response => response.json())
        .then(data => setData(data))
        .catch(err => console.error(err));
    }, [url])

    return data;
}

export default useVzlaData;